*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}

.app {
  min-height: 100vh;
  width: 100vw;
  background-color: rgb(24, 24, 24);
  position: relative;
}

.search_box{
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  padding: 3px;
  margin: 30px 0;
  border-radius: 5px;
}

.search_box input{
  width: 85%;
  padding:10px;
  border: none;
}

.search_box button{
  width: 15%;
  padding:10px;
  border: none;
  background-color: rgb(24, 24, 24);
  color: white;
  border-radius: 3px;
  box-shadow: 0 0 5px black;
}

._container{
  width: 80%;
  margin: auto;
}

.All_Images {
  display: flex;
  flex-wrap: wrap;
  margin: auto; 
}

.All_Images-col {
  padding: 10px; 
  width: calc(20% - 20px); 
}

.All_Images>div>div{
  margin: 10px  !important;
}

.All_Images>div>div:hover{
  opacity: 80%;
  cursor: pointer;
}

.All_Images img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  overflow: hidden;
}
.load{
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  background-color: rgba(44, 44, 44, 0.664);
}

.image_view{
  width: 80vw;
  height: 80vh;
  position: fixed;
  top: 10vh;
  left: 10vw;
  background-color: white;
  padding: 15px;
  border-radius: 10px;
  z-index: 999999;

}
.image_view img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 8px; /* Optionally, add border-radius for rounded corners */
}

.image_view button{
  position: absolute;
  border: none;
  top: 50%;
}

.image_view button i{
  font-size: 35px;
  transition: 0.25s all;
}

.image_view button i:hover{
  color: gray;
  transition: 0.25s all;
}

.image_view .left_btn{
left: 15px;
}
.image_view .right_btn{
  right: 15px;
}
.image_view .close{
  top: 15px !important;
  right: 15px;
}